
.worklist{
    ul{
        display: flex;
        user-select: none;
        // background-color:salmon;
        // height: 800px;
        flex-direction: column;
        margin-top: 30px;
        // justify-content: center;
        align-items: center;
        // align-items: flex-start;
        // flex-wrap: wrap;
         li{
            display: flex;
            width: 90%;    
            user-select: none;
            // left: 25% ;       
            margin-bottom: 5px;
            align-items: center;
            
            // background-color: royalblue;
            a{
              display: inline-flex;
              justify-content: space-between;
              flex-direction: column;
              align-items: center;
              // background-color: royalblue;
              .cover{
                img{
                  width:100%;
                  height:45%;
                  // border-radius: 7px;
                  margin-bottom: 10px;
                }
              }
              .workbrief{
                // position:absolute;
                width: 100%;
                text-align: left;
                // padding-left: 15px;
                h3{
                  font-size: 36px;
                  font-weight: 500;
                  // color: rgb(51, 51, 51);
                  // color: rgb(200, 200, 200);
                  color: rgb(250, 250, 250);


                }
                p{
                  // padding: 20px 0px;
                  padding-top: 10px;
                  font-weight: 400;
                  font-style: normal;
                  // color: rgba(0, 0, 0, 0.7);
                  color: rgb(200, 200, 200);
                  font-size: 18px;
                  line-height: 1.4;
                }
                .tag{
                  display: flex;
                  justify-content: flex-start;
                  flex-wrap: wrap;
                  margin-top: 5px;
                  span{
                    // color: rgba(0, 0, 0, 0.55);
                    color: rgba(0,255,0,0.5);
                    // font-family: 'IBMPlexSans';
                    font-style: italic;
                    font-weight: 500;
                    font-size: 13px;
                  }
                  span:not(:last-child)::after {
                    content: "•";
                    padding: 0px 0.5em;
                  }
                }
              }
            }
         }
      }
}