.App {
  // font-family: sans-serif;
  // font-family: 'Monument Grotesk';
  font-family: 'Roboto','IBM 3270';
  background-color: black;
  user-select: none;
  // background-color: blue;

  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  min-height: 100%;
  flex-direction: column;
  justify-content: space-between;
  .content{
    //  position: absolute;
     position: relative;
     flex: 1;
     width: 100%;
     background-color: black;
    //  background-color: yellow;
    //  display: flex;
    //  flex-direction: column;
    //  justify-content: center;
    //  align-items: center;
    //  object-fit: fill;
  }
  .header{
    position: relative;
    // top: 0;
    z-index: 1000;
    // background-color: rgb(250, 250, 250);
    // background-color: white;
    background-color: black;


    flex-shrink: 0;
    width: 100%;
    height: 60px;
    border-bottom: 1px dashed rgba(102, 102, 102,0.5);
  
    .selected{
      color: #333!important;
      padding-bottom: 5px;
      border-bottom: 1.5px solid #333;
    }
    .box{
      width: 85%;
      height: 100%;
      margin: 0px auto;
      padding: 0px 12px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      // background-color:red;
      a:hover { 
        text-decoration:none; 
      }
      .boxleft{
        // background-color:blue;
        font-size: 20px;
        font-weight: bold;
        color: rgb(51, 51, 51);
      }
      // .boxleft::after {
      //   content: "◦◦◦";
      //   margin-left: 0.5em;
      // }
      .boxright{
        display: inline-flex;
        justify-content: flex-start;
        margin-left: 5px;
        // position: relative;
        // right:0;
        p{
          margin: 0px 10px;
          font-size: 14px;
          font-weight: bold;
          color: rgb(102, 102, 102);
        }
      }
      div{
        cursor: pointer;
      }
    }
  }
  .footer{
    position: relative;
    z-index: 1000;
    // bottom: 0;
    text-align: center;
    flex-shrink:0;
    display: flex;
    justify-content: center;
    align-items: center;
    // height: 50px;

    width: 100%;
    padding: 15px 0px;
    color: #555;
    font-size: 13px;
  
    // background-color: rgb(250, 250, 250);
    background-color: black;
    // background-color: blue;
    // border-top: 1px solid #f2f2f2;
    // border-top: 1px solid rgb(10, 10, 10);
    // border-top: 1px dashed rgba(102, 102, 102, 0.5);
    div{
      // left: 20%;
      width: 70%;
      display:flex;
      justify-content: center;
      flex-direction: row;
      // background-color: blue;

      p{
        padding: 2px 2px;
      }
    }

    
  }
}

