
.wrap{
    flex: 1;
    // background-color:red;
    display: flex;
    flex-direction: column;
    position: relative;
    // left: 7.5%;
    width: 100%;
    height: 100%;
    align-items: center;
    // justify-content: center;
    
    .singlebox{
        // background-color:cadetblue;
        // margin-top:500px;
        cursor: pointer;
        width: 80%;
        margin: 0px auto;
        margin-top: 10px;
        margin-bottom: 40px;
        display: relative;
        z-index: 10000;
        // padding: 80px 80px;
        .title{
            h1{
                font-weight: 600;
                // font-style: italic;
                font-size: 30px;
                color:rgb(250, 250, 250);
                color: rgba(0,255,0,0.4);
                line-height: 1.5;
            }
            p{
                
                // color: rgb(150, 150, 150);
                color:rgb(200, 200, 200);
                color: rgba(0,255,0,0.45);
                // font-family: sans-serif;
                font-style: italic;
                // color: rgb(102, 102, 102);
                line-height: 1.5;
                font-weight: 300;

                // padding-top: 8px;
                font-size: 12px;
            }
        }
        .content{
            // padding: 20px 0px;
            p{
                // padding-top: 10px;
                line-height: 1.5;
                text-align: justify;
                font-weight: 300;
                font-size: 15px;
                font-style: italic;
                padding-top: 20px;
                // font-style: italic;
                // color:rgb(200, 200, 200);
                color: rgb(150, 150, 150);
                color: rgba(0,255,0,0.35);
            }
        }
    }
    .mutibox{
        // background-color:cadetblue;
        // margin-top:500px;
        // cursor: pointer;
        width: 90%;
        height: 100%;
        margin: 10px auto;
        // padding: 80px 0px;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        flex-wrap: wrap;
        .imagebox{
            display: flex;
            flex-direction: column;
            width: 100%;
            height: 100%;
            color: rgb(150, 150, 150);
            padding: 10px 0px;
            // background-color: darkkhaki;
            .content{
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                align-items: flex-start;
                padding: 20px 0px;
                div{
                    width: 20%;
                    padding-left: 80px;
                    padding-right: 30px;
                    p{
                        line-height: 1.8;
                        text-align: justify;
                        font-weight: 300;
                        font-size: 12px;
                        font-style: italic;
                        padding-top: 20px;
                        // font-style: italic;
                        // color:rgb(200, 200, 200);
                        color: rgb(150, 150, 150);
                    }
                    
                }
                img{
                    width: 50%;
                    opacity: 40%;
                    padding-left: 10px;
                    // border-radius: 5px;
                }
            }
        }
        .infobox{
            width: 72%;
            left: 40%;
            height: 90%;
            text-align: justify;
            padding: 50px 0px;
            color: rgb(150, 150, 150);
            // background-color: darkmagenta;
            .content{
                padding: 20px 0px;
                p{
                    // padding-top: 10px;
                    line-height: 1.4;
                    text-align: justify;
                    font-weight: 300;
                    font-size: 12px;
                    font-style: italic;
                    padding-top: 8px;
                    // font-style: italic;
                    // color:rgb(200, 200, 200);
                    color: rgb(150, 150, 150);
                }
                a{
                    // padding-top: 10px;
                    line-height: 1.8;
                    text-align: justify;
                    font-weight: 400;
                    font-size: 12px;
                    font-style: normal;
                    // text-decoration: underline;
                    // padding-top: 20px;
                    // padding-bottom: 500px;
                    // padding-right: 20px;
                    // font-style: italic;
                    // color:rgb(200, 200, 200);
                    color: rgb(150, 150, 150);

                }
                img{
                    width:100%;
                    opacity: 80%;
                    // border-radius: 5px;
                }
            }
        }
    }
}