.wrap{
   flex: 1;
   width: 100%;
  //  height:100%;
  user-select: none;
   display:flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
  //  height: 100%;
   background-color: rgb(0, 0, 0);
   margin: 0px auto;
   text-align: center;
   padding-top: 40px;
   padding-bottom: 25px;
  //  justify-content: center;
   .title{
      padding-bottom: 20px;
      img{
        width: 150px;
        height: 150px;
        border-radius: 120px;
        margin-top: 20px;
        margin-bottom: 5px;
     }
      h3{
        font-size: 30px;
        padding-bottom: 12px;
        // color: rgba(0,0,0,.8);
        color:rgb(200, 200, 200);
        font-style: italic;
        font-weight: 500;
      }
      p{
        // color: rgba(0, 0, 0, 0.7);
        font-size: 18px;
        font-weight: normal;
        font-style: italic;
        color:rgb(200, 200, 200);
      }
   }
   
}