
.worklist{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  user-select: none;
  // background-color: blue;
  width: 100%;
    ul{
        display: flex;
        width: 90%;
        // left:15%;
        // background-color:salmon;
        // height: 800px;
        flex-direction: column;
        
        margin-top: 10px;
        padding: 20px 0px;
        justify-content: space-between;
        // justify-content: center;
        // align-items: center;
        align-items: flex-start;
        // flex-wrap: wrap;
         li{
            display: flex;
            flex-direction: column-reverse;
            // justify-content: space-between;

            width: 100%;     
            padding-top: 5px;  
            padding-bottom: 5px;
            // padding-left: 5px;
            // border-bottom: 1px dashed rgba(102, 102, 102,0.75);
            // margin-top: 10px;     
            // margin-bottom: 15px;
            // background-color: royalblue;
            a{
              display: inline-flex;
              justify-content: space-between;
              // flex-direction: column;
              // align-items: flex-start;
              .cover{
                img{
                  // width: 500px;
                  height:100px;
                  // border-radius: 5px;
                }
              }
              .workbrief{
                text-align: left;
                padding-left: 15px;
                // justify-content: space-between;
                h3{
                  font-size: 36px;
                  font-weight: 300;
                  // color: rgb(51, 51, 51);
                  // color: rgb(200, 200, 200);
                  color: rgb(250, 250, 250);
                  margin-top: 3px;
                  margin-bottom: 5px;
                }
                p{
                  // padding: 20px 0px;
                  padding-top: 3px;
                  font-weight: 400;
                  font-style: italic;
                  // color: rgba(0, 0, 0, 0.7);
                  color: rgb(200, 200, 200);
                  font-size: 16px;
                  line-height: 1.5;
                }
                .tag{
                  display: flex;
                  justify-content: flex-start;
                  flex-wrap: wrap;
                  margin-top: 5px;
                  span{
                    // color: rgba(0, 0, 0, 0.55);
                    color: rgba(0,255,0,0.5);
                    // font-family: 'IBMPlexSans';
                    font-style: italic;
                    font-weight: 500;
                    font-size: 13px;
                  }
                  span:not(:last-child)::after {
                    content: "•";
                    padding: 0px 0.5em;
                  }
                }
              }
            }
         }
      }
}